/**
 * Subtracts second param from first.
 * @param  {number} a - First number.
 * @param  {number} b - Second number.
 * @return {number} - Value of `b` subtracted from `a`.
 */
const subtract = (a, b) => Number(a) - Number(b);

export default subtract;



